* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card p, .card h2 {
  margin: 0;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin-bottom: 23px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__buy-button {
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  padding: 12px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .star:first-child, .stars--2 .star:nth-child(-n+2), .stars--3 .star:nth-child(-n+3), .stars--4 .star:nth-child(-n+4), .stars--5 .star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
